<template>
	<div class="blockmain">
		<div class="blockforms">
		<input v-model="email" placeholder="Электронная почта" class="inputtext" type="text" name="Login">
		<input v-model="password" placeholder="Пароль" class="inputtext" type="password" name="Password">
		<center>
			<div class="eror">{{texteror}}</div>
		</center>
		<button class="enterbutton" v-on:click="submitHandler()">Войти</button>
		<div class="textnotaccaunt">Нет аккаунта? <button>Зарегистрироваться</button></div>
		</div>
	</div>
</template>

<style>
.blockmain{
display:flex;
justify-content: center;
}
.blockforms{
margin-top:10%;
position: fixed;
background:rgba(22,22,34,.9);
width:30%;
height:40%;
border-radius:15px;
padding-top:3%;
}
.inputtext{
	width:80%;
	height:10%;
	border-radius:5px;
	border:solid 1px silver;
	font-size:24px;
	margin-left:10%;
	margin-top:10%;
}
.enterbutton{
	width: 42%;
	height:12%;
	margin-top: 10%;
	margin-left:28%;
	font-size:24px;
	color:white;
	border:orange;
	border-radius:5px;
	background: linear-gradient(45deg, rgba(255,150,53,.8), rgba(255,94,58,.5));
}
.enterbutton:active{
background:rgba(255,150,53,.8);
}
.textnotaccaunt{
color: white;
margin-left:30%;
margin-top: 2%;
}
.textnotaccaunt button{
color: white;
background: rgba(22,22,34,.1);
border:solid 1px rgba(22,22,34,.1);
}
.eror{
	color: red;
	margin-top:2%;
	font-size:18px;
}
@media screen and (max-width: 426px) {
.blockforms{
margin-top:40%;
position: fixed;
background:rgba(22,22,34,.9);
width:90%;
height:40%;
border-radius:15px;
padding-top:3%;
}
.enterbutton{
	width: 70%;
	height:15%;
	margin-top: 10%;
	margin-left:15%;
	font-size:24px;
	color:white;
	border:orange;
	border-radius:5px;
	background: linear-gradient(45deg, rgba(255,150,53,.8), rgba(255,94,58,.5));
}
}
</style>

<script>
import {aftorizbool} from '../main'
export default {
  name: 'aftorization',
  data(){
    return{
   aftorizboolin:false, 
   email:'',
   password:'',
   texteror:''
    }
  },
methods:{
async submitHandler(){
	
const formData = {
email: this.email,
password: this.password
}
try{
this.aftorizboolin = true;
aftorizbool.$emit('submiteHandler', this.aftorizboolin);
await this.$store.dispatch('login',formData);
this.$router.push('/webmuse');
this.aftorizboolin = false;
}
catch{
this.texteror="Не правельный логин или пароль!"
console.log('eror')
}
}	
}
}	
</script>