import Vue from 'vue'
import VueRouter from 'vue-router'
import Maincomponent from '../components/maincomponent.vue'
import Aftorization from '../components/aftorization.vue'
import {aftorizbool} from '../main'

Vue.use(VueRouter)

const routes = [
  {
  path: '/',
  name: 'Aftorization',
  component: Aftorization,
  meta: {auth:false}
  },
  {
    path: '/webmuse',
    name: 'Maincomponent',
    component: Maincomponent,
    meta: {auth:true}
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

let log;
router.beforeEach((to, from, next)=>{

aftorizbool.$on('submiteHandler', data=>{  
  log = data;
});
const reqireAuth = to.matched.some(record => record.meta.auth)
if(reqireAuth && !log){
next('/');
}
else{
  next();
  log = false;
}
})

export default router