<template>
<div>
<div class="maincontent">
  <div id="video-placeholder"></div>
  <div id="video-placeholder2"></div>
</div>
<footer>

<div>
  <progress class="loadvideo" value="0" max="100"/>
</div>

<div class="playermuse">
  <button>></button>
  <button id="start">||</button>
  <button id="rightenter">></button>
  <div class="titlevidos">{{titlevidos}}</div>
</div>
   
</footer>

</div>
</template>
<style>
.maincontent{
height:85%;
padding-top:3%;
}

.loadvideo{
  width:100%;
  margin-left: 0%;
  background: linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
  margin-bottom:5px;
  height:8px;
}
.loadvideo {
background: linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
}
/* Firefox */
.loadvideo::-moz-progress-bar {
background: linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
}
/* Chrome */
.loadvideo::-webkit-progress-value {
background:linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
}
footer{
background-color:rgba(22,22,34,.9);
height:10%;
width:100%;
position:absolute;
z-index:2;
}
.vidios{
  margin-left:40%;
  width: 30%;
  height:70%;
  padding-top:5%;
  position:fixed;
}
.start{
margin-top:5%;
}
.playermuse{
display: flex;
margin-top:0.5%;
margin-left:2%;
}
.playermuse button{
margin-left:1%;
font-size:24px;
border:solid 2px white;
background:linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
color:white;
border-radius:100%;
width:40px;
height:40px;
}

#video-placeholder{
  width:70%;
  height:100%;
}
.titlevidos{
color: white;
margin-left:2%;
font-size:20px;
}
@media screen and (max-width: 769px) {
nav button{
display: none;
margin-right:10px;
margin-top:3%;
width:150px;
height:30px;
color:white;
background:linear-gradient(45deg, rgb(255,150,53), rgb(255,94,58));
box-shadow:2px 2px 10px rgb(255,150,53);
border:solid 2px;
border-radius:15px;
border-top:none;
border-bottom: none; 
border-left:rgb(255,150,53);
border-right:rgb(255,94,58);
}
.maincontent{
height:88%;
padding-top:3%;
}
#video-placeholder{
  width:100%;
  height:50%;
}
.titlevidos{
color: white;
margin-left:2%;
font-size:14px;
}
}
</style>

<script src="https://www.youtube.com/iframe_api"></script>
<script>

export default {
  name: 'vidiosplayblock',
  props: {
    vidos:[],
    titlevidos:''
  },
  data(){
    return{

    }
  },



  mounted(){
let start = document.querySelector('#start');
let rightenter = document.querySelector('#rightenter');
//let music = 'B0vCVZPBqO0';
let music = this.vidos[0];
this.titlevidos = this.vidos[1];
let startend = false;

let player;
onYouTubeIframeAPIReady(music);
function onYouTubeIframeAPIReady(music) {
player = new YT.Player('video-placeholder', {

videoId: music,
playerVars: {
autoplay: true,
color: 'white',
playlist: music,
    },
    events: {
        onReady: initialize
        }
    });
}

rightenter.onclick = function(){
let music = 'a7_e_NY-f3g';
player.stopVideo();
player.destroy();
onYouTubeIframeAPIReady(music);
}

start.onclick = function(){
if(startend == false){
player.playVideo();
startend = true;
}
else{
player.pauseVideo();
startend = false;
}
}

function initialize(){


let loadvideo = document.querySelector('.loadvideo');

//onYouTubeIframeAPIReady2();
    // Обновляем элементы управления и загружаем
    //updateTimerDisplay();
 

    // Сброс старого интервала
 //   clearInterval(time_update_interval);
    // Запускаем обновление таймера и дорожки проигрывания
    // каждую секунду.
    time_update_interval = setInterval(function () {
    //    updateTimerDisplay();
       
       loadvideo.value = (player.getCurrentTime() / player.getDuration()) * 100;
    }, 100)    
  }
}
}

</script>